.container {
  transition-property: background-color, border-color, color, transform;
  transition-duration: 250ms;
  cursor: pointer;
  outline: none; }

.container {
  height: 4em;
  padding: 0;
  font: inherit;
  width: 4em;
  box-sizing: border-box;
  text-align: center;
  align-items: flex-start;
  display: block;
  background-color: transparent;
  color: #2b303a;
  border-color: transparent; }
  .container:hover, .container:focus {
    transform: translate(0, -0.1rem); }
  .container:active {
    transition-duration: 100ms;
    transform: translate(0, 0.1rem); }
  .container:hover {
    background-color: #d9d9d9;
    color: #2b303a;
    border-color: transparent; }
  .container:focus {
    background-color: #F0c808;
    color: #2b303a;
    border-color: transparent; }

.inactive {
  opacity: 0.25; }

.disabled {
  pointer-events: none; }

.activecell {
  border: 0.1em solid #e0ddcf;
  border-bottom-color: white;
  position: relative;
  z-index: 1; }

.label {
  text-align: center; }

.preview {
  font-size: 2em; }

.grid {
  position: relative;
  height: 100%;
  overflow: auto;
  min-height: 6em; }

.details {
  display: block;
  padding: 1em;
  box-sizing: border-box;
  margin-top: -0.05em;
  border-top: 0.05em solid #e0ddcf;
  border-bottom: 0.05em solid #e0ddcf; }

.detailsspacer, .details {
  height: auto; }

.detailsspacer {
  float: left;
  width: 100%; }

.gridcell, .textcell {
  width: 4em;
  display: inline-block;
  vertical-align: top; }

.emptyset {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.textcell {
  height: 4em;
  padding-top: 1.5em;
  box-sizing: border-box; }

@media print {
  .grid {
    height: auto;
    overflow: visible; } }
