.textarea {
  border: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  margin-top: -3px;
  margin-bottom: -3px;
  height: calc(100% + 6px) !important;
  overflow: auto; }
  .textarea:focus {
    border-top: 3px solid #4392F1;
    border-bottom: 3px solid #4392F1; }

.textfield {
  width: 100%;
  display: block;
  border: 0;
  border-bottom: 3px solid transparent;
  margin-bottom: -3px;
  background: transparent;
  outline: none;
  font-size: 1.2em; }
  .textfield:focus {
    border-bottom: 3px solid #4392F1; }

.textareacontainer, .textfieldcontainer {
  border: 0;
  transition: border 150ms ease-in-out, padding 150ms ease-in-out; }

.textareacontainer, .textfieldcontainer {
  border-bottom: 1px solid #e0ddcf;
  padding-bottom: 1px; }
  .textareacontainer:hover, .textfieldcontainer:hover {
    border-bottom-width: 2px;
    padding-bottom: 0px;
    border-bottom-color: #2b303a; }

.textareacontainer {
  border-top: 1px solid #e0ddcf;
  padding-top: 1px; }
  .textareacontainer:hover {
    border-top-width: 2px;
    padding-top: 0px;
    border-top-color: #2b303a; }

.textareacontainer {
  height: 100%;
  max-height: 50vh; }

.textfieldcontainer {
  margin-bottom: 1em; }

.vcell, .vcursor {
  width: 1em;
  height: 1em;
  text-align: center; }

@keyframes vcursorblinkanimation {
  to {
    opacity: 0; } }

.vcursor {
  display: none;
  background-color: #4392F1;
  animation: vcursorblinkanimation 0.5s steps(2, start) infinite; }

.vline {
  width: 1em;
  display: inline-block; }

.vcontainer {
  direction: rtl !important;
  display: flex !important;
  justify-content: center;
  overflow: auto; }
  .vcontainer:focus .vcursor {
    display: block; }
