.track {
  height: 4px;
  width: 100%;
  background-color: #e0ddcf;
  position: relative; }

.runner {
  background-color: #4392F1;
  position: absolute;
  height: 100%;
  transition: width 150ms ease-in-out; }

.runneractive {
  transition: none; }

.trackcontainer {
  height: 4px;
  padding: 8px 0;
  cursor: pointer;
  margin-bottom: 1em; }

.thumb {
  width: 16px;
  height: 16px;
  background-color: #4392F1;
  border-radius: 100%;
  position: relative;
  top: -6px;
  z-index: 0;
  border: 2px solid #106bd7;
  box-sizing: border-box;
  transition: left 150ms ease-in-out; }
  .thumb:after {
    content: ' ';
    width: 16px;
    height: 16px;
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: #4392F1;
    opacity: 0.3;
    border-radius: 100%;
    transform: translate(-50%, -50%);
    transition: transform 150ms ease-in-out;
    z-index: -1; }
  .thumb:hover:after {
    transform: translate(-50%, -50%) scale(2); }
  .thumb.thumbactive {
    transition: none; }
  .thumb.thumbactive:after {
    transform: translate(-50%, -50%) scale(3) !important; }

.toggle {
  width: 48px;
  margin: 0 0.5em; }

.togglecheckbox {
  display: none; }

.togglecontainer {
  display: flex;
  transition: opacity 150ms ease-in-out; }

.disabled {
  pointer-events: none;
  opacity: 0.25; }
  .disabled .runner, .disabled .thumb {
    background-color: #8a8159; }
  .disabled .thumb {
    border: #8a8159; }
