.default, .success, .danger {
  transition-property: background-color, border-color, color, transform;
  transition-duration: 250ms;
  cursor: pointer;
  outline: none; }

.badge {
  margin: -0.5em 0em;
  padding: 0.25em 0.5em;
  pointer-events: none; }

.default {
  background-color: #4c5466;
  color: white;
  border-color: transparent; }
  .default:hover, .default:focus {
    transform: translate(0, -0.1rem); }
  .default:active {
    transition-duration: 100ms;
    transform: translate(0, 0.1rem); }
  .default:hover {
    background-color: #2b303a;
    color: white;
    border-color: transparent; }
  .default:focus {
    background-color: #F0c808;
    color: #2b303a;
    border-color: #2b303a; }

.success {
  background-color: #4392F1;
  color: white;
  border-color: transparent; }
  .success:hover, .success:focus {
    transform: translate(0, -0.1rem); }
  .success:active {
    transition-duration: 100ms;
    transform: translate(0, 0.1rem); }
  .success:hover {
    background-color: #106bd7;
    color: white;
    border-color: transparent; }
  .success:focus {
    background-color: #F0c808;
    color: #2b303a;
    border-color: #2b303a; }

.danger {
  background-color: #dd1c1a;
  color: white;
  border-color: transparent; }
  .danger:hover, .danger:focus {
    transform: translate(0, -0.1rem); }
  .danger:active {
    transition-duration: 100ms;
    transform: translate(0, 0.1rem); }
  .danger:hover {
    background-color: #991312;
    color: white;
    border-color: transparent; }
  .danger:focus {
    background-color: #F0c808;
    color: #2b303a;
    border-color: #2b303a; }
